import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ProductionVoucherComponent } from './production-voucher.component';
import { SharedModule } from '../../../shared/modules/shared.module';
import { ProductionVoucherRoutingModule } from './production-voucher-routing.module';
import { ProductionVoucherAddComponent } from './production-voucher-add/production-voucher-add.component';
import { ProductionVoucherViewComponent } from './production-voucher-view/production-voucher-view.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    ProductionVoucherRoutingModule
  ],
  declarations: [
    ProductionVoucherComponent,
    ProductionVoucherAddComponent,
    ProductionVoucherViewComponent
  ],
  entryComponents:[
    ProductionVoucherAddComponent,
    ProductionVoucherViewComponent
  ]
})
export class ProductionVoucherModule { }



