import { Component, OnInit, Inject, Output, EventEmitter, ViewChild } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators, FormArray } from '@angular/forms';
import { ProductionVoucherService, CurrentUserService, ErrorHandleService, ItemMasterService, BillOfMaterialService, StoreService } from '../../../../shared';
import { jqxValidatorComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxvalidator';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';
import { jqxDateTimeInputComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxdatetimeinput';

@Component({
  selector: 'app-production-voucher-add',
  templateUrl: './production-voucher-add.component.html',
  styleUrls: ['./production-voucher-add.component.scss']
})
export class ProductionVoucherAddComponent implements OnInit {

  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('myValidator') myValidator: jqxValidatorComponent;
  @ViewChild('msgNotification') msgNotification: jqxNotificationComponent;
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;
  @ViewChild('bomComboBox') bomComboBox: jqxComboBoxComponent;
  @ViewChild('storeComboBox') storeComboBox: jqxComboBoxComponent;
  @ViewChild('dateInput') dateInput: jqxDateTimeInputComponent;
  @Output() AddedData = new EventEmitter();
  @Output() cancelData = new EventEmitter();

  pvForm: FormGroup;
  apiUrl: string;

  inputItemAdapter: any = [];
  inputItemFocus: any = [];

  outputItemAdapter: any = [];
  outputItemFocus: any = [];

  bomAdapter: any = [];
  storeAdapter: any = [];

  currentInputArray: any = [];
  currentOutputArray: any = [];
  currentRatio: number;

  valueTotal: any = {};
  labourCost = 0;
  exCost = 0;
  showDiv: boolean = false;

  extraCostForm: FormGroup;
  extraCost: any;
  total: any;

  storeSelected: boolean = false;

  // outputTotal:number = 0;

  constructor(
    private fb: FormBuilder,
    private ehs: ErrorHandleService,
    private cus: CurrentUserService,
    private ims: ItemMasterService,
    private pvs: ProductionVoucherService,
    private boms: BillOfMaterialService,
    private ss: StoreService,
    @Inject('API_URL') apiUrl: string
  ) {
    this.apiUrl = apiUrl;
    this.createForm();
  }

  ngOnInit() {
    this.ss.index({ limit: 'all' }).subscribe(
      response => {
        // console.log(response);
        this.storeAdapter = response;
      },
      error => {
        console.log(error);
      }
    )

    this.extraCostForm = this.fb.group({
      extraCost: this.fb.array([this.createFormGroupItems()])
    })
  }

  ngAfterViewInit() {
    this.jqxLoader.open();
    this.boms.all().subscribe(
      response => {
        // console.log(response);
        let rawData = response['data'];
        for (let i = 0; i < rawData.length; i++) {
          rawData[i].display_field = rawData[i].name + ' - ' + rawData[i].code;
        }
        this.bomAdapter = rawData;
        this.jqxLoader.close();
      },
      error => {
        console.log(error);
        this.jqxLoader.close();
      }
    );
  }

  createForm() {
    this.pvForm = this.fb.group({
      'input_materials': this.fb.array([
        this.initMaterials(),
      ]),
      'output_materials': this.fb.array([
        this.initMaterials(),
      ])
    })
  }


  initMaterials() {
    return this.fb.group({
      item_name: [null, Validators.required],
      item_id: [null, Validators.required],
      qty: [null, Validators.required],
      actual_qty: [null, Validators.required],
      rate: [null, Validators.required],
      value: [null, Validators.required]
    });
  }

  /**
 * inputItemFilter Event is called when Item input field has
 * keyup action followed by 'Space'
 * Generate Remote Suggestion based on input value entered
 * @param searchString 
 * @param index 
 */
  inputItemFilter(searchPr, index) {
    let keycode = searchPr['keyCode'];
    if ((keycode == 40)) {
      document.getElementById('itemCode1').focus();
    }
    let searchString = searchPr['target'].value;
    let len = searchString.length;
    let dataString = searchString.substr(len - 1, len);
    let temp = searchString.replace(' ', '');
    temp = temp.replace(/\s/g, '');
    if (dataString == ' ' && temp.length > 2) {
      if (temp) {
        this.inputItemFocus[index] = true;
        this.ims.index({ code: temp, transact: 1, limit: 'all' }).subscribe(
          response => {
            // console.log(response);
            this.inputItemAdapter[index] = [];
            this.inputItemAdapter[index] = response;
            // console.log(this.inputItemAdapter);
          },
          error => {
            console.log(error);
          }
        );
      } else {
        this.inputItemFocus[index] = false;
      }
    }
  }

  outputItemFilter(searchPr, index) {
    let keycode = searchPr['keyCode'];
    if ((keycode == 40)) {
      document.getElementById('itemCode2').focus();
    }
    let searchString = searchPr['target'].value;
    let len = searchString.length;
    let dataString = searchString.substr(len - 1, len);
    let temp = searchString.replace(' ', '');
    temp = temp.replace(/\s/g, '');
    if (dataString == ' ' && temp.length > 2) {
      if (temp) {
        this.outputItemFocus[index] = true;
        this.ims.index({ code: temp, transact: 1, limit: 'all' }).subscribe(
          response => {
            this.outputItemAdapter[index] = [];
            this.outputItemAdapter[index] = response;
          },
          error => {
            console.log(error);
          }
        );

      } else {
        this.outputItemFocus[index] = false;
      }
    }
  }

  /**
   * Event fired when option is selected from Item Suggestion Select field
   * Hide Select field after Item Selected.
   * @param selectedValue 
   * @param index 
   */
  inputItemListSelected(selectedValue, index) {
    // console.log(selectedValue);
    if (selectedValue) {
      this.pvForm.get('input_materials')['controls'][index].get('item_id').patchValue(selectedValue.toFixed(2));
      // this.inputItemFocus[index] = false;
    }
  }

  outputItemListSelected(selectedValue, index) {
    // console.log(selectedValue);
    if (selectedValue) {
      this.pvForm.get('output_materials')['controls'][index].get('item_id').patchValue(selectedValue.toFixed(2));
      // this.outputItemFocus[index] = false;
    }
  }

  /**
   * Add FormGroup to Requisition Item FormArray
   * Increments   Item FormArray
   */
  addItem(arrayName) {
    const control1 = <FormArray>this.pvForm.controls[arrayName];
    control1.push(this.initMaterials());
    // console.log(control1.length);
  }

  /**
   * Remove FormGroup at particular position form Requisition Item FormArray
   * Decrements   Item FormArray
   */
  removeItem(i: number, arrayName) {
    const control1 = <FormArray>this.pvForm.controls[arrayName];
    control1.removeAt(i);
    /**
     * Remove itemAdapter itemArray at Particular position i, 
     * Select Field option for select field at position 'i' of formArray
     */
    if (arrayName == 'output_materials') {
      this.outputItemAdapter.splice(i, 1);
    } else {
      this.inputItemAdapter.splice(i, 1);
    }

  }


  bomChange(event) {
    this.showDiv = true;
    this.pvForm.setControl('input_materials', this.fb.array([]));
    this.pvForm.setControl('output_materials', this.fb.array([]));
    this.currentInputArray = [];
    this.currentOutputArray = [];

    // console.log(event);
    let comboVal = this.bomComboBox.val() || null;
    if (comboVal) {
      let currentBomData = this.bomAdapter.filter(x => x.id == comboVal)[0];
      let bomData = {};
      bomData['input_materials'] = [];
      bomData['output_materials'] = [];
      if (currentBomData && currentBomData.bom_items && currentBomData.bom_items.length > 0) {
        this.jqxLoader.open();
        for (let i = 0; i < currentBomData.bom_items.length; i++) {
          let itemData = {};
          itemData['qty'] = 0;
          itemData['actual_qty'] = currentBomData.bom_items[i].qty || 0;
          itemData['rate'] = 0;
          itemData['value'] = 0;
          itemData['item_id'] = currentBomData.bom_items[i].item_id;
          itemData['item_name'] = currentBomData.bom_items[i].item && currentBomData.bom_items[i].item.name + ' (' + currentBomData.bom_items[i].item.code + ')';
          if (currentBomData.bom_items[i].type == 1) {
            bomData['input_materials'].push(itemData);
            this.currentInputArray.push(currentBomData.bom_items[i]);
            this.calculateValue(currentBomData.bom_items[i].item_id, 'input_materials', bomData['input_materials'].length - 1);
          }
          if (currentBomData.bom_items[i].type == 0) {
            bomData['output_materials'].push(itemData);
            this.currentOutputArray.push(currentBomData.bom_items[i]);
            this.calculateValue(currentBomData.bom_items[i].item_id, 'output_materials', bomData['output_materials'].length - 1);
          }
        }
        // console.log(this.currentInputArray);
        // console.log(this.currentOutputArray);

        // Creating formArray
        for (let i = 0; i < (this.currentInputArray.length); i++) {
          this.addItem('input_materials');
        }

        // Creating formArray
        for (let i = 0; i < (this.currentOutputArray.length); i++) {
          this.addItem('output_materials');
        }

        // console.log(bomData);
        this.pvForm.setValue(bomData);
        this.jqxLoader.close();
      }

    }
  }

  calculateValue(itemId, arrayName, inputIndex) {
    // console.log('item INdex : '+ inputIndex);
    // this.jqxLoader.open();
    let date = this.dateInput.val();
    this.ims.showItem(itemId, date).subscribe(
      response => {
        let itemRate = response && response['data'] && response['data'].rate.toFixed(2) || 0;
        this.pvForm.get(arrayName)['controls'][inputIndex].get('rate').patchValue(itemRate);
        // console.log(response);
        // this.jqxLoader.close();
      },
      error => {
        console.log('error');
        // this.jqxLoader.close();
      }
    )
  }

  optRateChange(value, index) {
    let qty = this.pvForm.get('output_materials')['controls'][index].get('qty').value || 0;
    let rate = this.pvForm.get('output_materials')['controls'][index].get('rate').value || 0;
    let currentvalue = qty * rate;
    // console.log(currentvalue);
    this.pvForm.get('output_materials')['controls'][index].get('value').patchValue(currentvalue.toFixed(2));
    this.calculateTotal('output_materials');
  }

  inptRateChange(value, index) {
    this.getLabourCost(null, null);
    let qty = this.pvForm.get('input_materials')['controls'][index].get('qty').value || 0;
    let rate = this.pvForm.get('input_materials')['controls'][index].get('rate').value || 0;
    let currentvalue = qty * rate;
    // console.log(currentvalue);
    this.pvForm.get('input_materials')['controls'][index].get('value').patchValue(currentvalue.toFixed(2));
    // this.getLabourCost(null, null);
    this.calculateTotal('input_materials');
  }

  opQtyChange(value, index) {
    let length = this.pvForm.get('output_materials')['controls'].length;
    for (let i = 0; i < length; i++) {
      this.calculateRate(
        parseFloat(this.pvForm.get('output_materials')['controls'][i].get('value').value),
        parseFloat(this.pvForm.get('output_materials')['controls'][i].get('qty').value),
        i
      );
    }

    //this.optRateChange(value, index);
    if (value > 0) {

      //Get Current quantity qty at 'index' of output items
      // console.log('-------- CUrrent OP Item ---------- ');
      // console.log(this.currentOutputArray[index]);
      let outputQtyAtIndex = this.currentOutputArray[index].qty;
      // console.log('output quantity at' + index + ' :' + outputQtyAtIndex);

      //create current ratio
      this.currentRatio = Number(value) / outputQtyAtIndex;
      // console.log('current Ratio :' + this.currentRatio);

      for (let i = 0; i < this.currentOutputArray.length; i++) {
        if (i != index) {
          // output value to be set in qty input field of Output Items
          // console.log('---- Op Data '+ i +' ----');
          // console.log(this.currentOutputArray[i]);
          // console.log('is not number ratio : '+ isNaN(this.currentRatio));
          // console.log('actual Qty: ' + this.currentOutputArray[i].qty);
          let actualQuantity: number = parseFloat("0" + this.currentOutputArray[i].qty);
          // console.log('is not number ratio : '+ isNaN(actualQuantity));
          let currentOutputValue: number = this.currentRatio * actualQuantity;
          // console.log('current OP Value :' + currentOutputValue);
          // console.log('is not number OP value: '+ isNaN(currentOutputValue));

          this.pvForm.get('output_materials')['controls'][i].get('qty').patchValue(currentOutputValue.toFixed(2));
        }
        //this.optRateChange(null, i);

      }
      //this.calculateTotal('output_materials');

      for (let i = 0; i < this.currentInputArray.length; i++) {
        // Input value to be set in qty input field of Input Items
        let currentInputValue: number = this.currentRatio * parseFloat("0" + this.currentInputArray[i].qty);
        // console.log('current IP Value :' + currentInputValue);

        this.pvForm.get('input_materials')['controls'][i].get('qty').patchValue(currentInputValue.toFixed(2));
        this.inptRateChange(null, i);
      }
      //this.calculateTotal('input_materials');

    }

  }

  /**
   * 
   * Pass FormArray Name
   * @param arrayName 
   */
  calculateTotal(arrayName) {
    let controlLength = this.pvForm.get(arrayName)['controls'].length;
    let controlTotal: number = 0;
    for (let i = 0; i < controlLength; i++) {
      controlTotal += parseFloat(this.pvForm.get(arrayName)['controls'][i].get('value').value);
    }

    this.valueTotal[arrayName] = controlTotal;
    // console.log(this.valueTotal);
  }

  /**
   * Check total Value of Input and Output items,
   * compares them and return boolean value based on condition.
   */
  checkTotalValidity() {
    let inputTotal = this.valueTotal && this.valueTotal['input_materials'] || null;
    let outputTotal = this.valueTotal && this.valueTotal['output_materials'] || null;
    if (inputTotal && (inputTotal > 0) && outputTotal && (outputTotal > 0) && inputTotal == outputTotal) {
      return true;
    } else {
      return false;
    }
  }

  /**
   * Function triggered when save button is clicked
   * @param formData 
   */
  save(formData) {
    // console.log(formData);
    formData['date'] = this.dateInput.val();
    formData['bom_id'] = this.bomComboBox.val() || null;
    formData['store_id'] = this.storeComboBox.val() || null;
    //formData['labour_cost'] = this.labourCost;
    //formData['extraCost'] = this.exCost;
    formData['extraCost'] = this.extraCostForm.value['extraCost'];
    // formData['lot_no'] = '1/' + this.dateInput.val();
    if (formData['bom_id'] && formData['date'] && formData['store_id']) {
      formData['pvItems'] = [];
      for (let i = 0; i < formData['input_materials'].length; i++) {
        formData['input_materials'][i].type = 1;
        delete formData['input_materials'][i].item_name;
        formData['pvItems'].push(formData['input_materials'][i]);
      }

      for (let i = 0; i < formData['output_materials'].length; i++) {
        formData['output_materials'][i].type = 0;
        delete formData['output_materials'][i].item_name;
        formData['pvItems'].push(formData['output_materials'][i]);
      }

      // removing unwanted fields for formObject;
      delete formData['input_materials'];
      delete formData['output_materials'];

      // console.log(formData);
      this.jqxLoader.open();
      this.pvs.store(formData).subscribe(
        result => {
          let data = this.ehs.formatError(result);
          // console.log(data);
          if (data['result'] == true) {
            this.AddedData.emit(data);
          } else {
            let messageDiv: any = document.getElementById('windowMessage');
            messageDiv.innerHTML = '';
            for (let i = 0; i < data['message'].length; i++) {
              let errorDiv = document.createElement('li');
              errorDiv.innerText = data['message'][i];
              messageDiv.appendChild(errorDiv);
            }
            this.winNotification.open();
          }
          this.jqxLoader.close();

        },
        error => {
          this.jqxLoader.close();
          console.info(error);
        }
      );

    } else {
      let messageDiv: any = document.getElementById('message');
      messageDiv.innerText = 'Select Bill of Material / Date';
      this.msgNotification.open();
      this.jqxLoader.close();
    }

  }

  cancelBtn() {
    this.cancelData.emit(true);
  }

  getLabourCost($event, i) {
    let total: number = 0;
    this.extraCostForm.get('extraCost').value.forEach(result => {
      total += parseFloat(result.cost);
    })

    this.total = (parseFloat(this.valueTotal['input_materials']) + total).toFixed(2);
  }

  

  //extracost
  addExtraCost() {
    this.extraCost = this.extraCostForm.controls['extraCost'] as FormArray;
    this.extraCost.push(this.createFormGroupItems());
  }

  removeExtraCost(i) {
    this.extraCost.removeAt(i);
  }

  createFormGroupItems(): FormGroup {
    return this.fb.group({
      item_name: [null, Validators.required],
      cost: [null, Validators.required]
    })
  }

  optValueChange() {
    let length = this.pvForm.get('output_materials')['controls'].length;
    let totalValue = 0;

    for (let i = 0; i < length; i++) {
      totalValue += parseFloat(this.pvForm.get('output_materials')['controls'][i].get('value').value);
      this.calculateRate(
        this.pvForm.get('output_materials')['controls'][i].get('value').value,
        this.pvForm.get('output_materials')['controls'][i].get('qty').value,
        i
      );
    }
    this.valueTotal['output_materials'] = totalValue.toFixed(2);
  }

  calculateRate(value, quantity, i) {
    let rate = (parseFloat(value)/parseFloat(quantity)).toFixed(2);
    if (rate == "Infinity")
      this.pvForm.get('output_materials')['controls'][i].get('rate').patchValue(0);
    else this.pvForm.get('output_materials')['controls'][i].get('rate').patchValue(rate);
  }

  storeItemSelected($event) {
    if (this.storeComboBox.val()) this.storeSelected = true;
    else this.storeSelected = false;
  }

}




