import { Component, OnInit, Inject, Input, Output, EventEmitter, ViewChild, ChangeDetectorRef } from '@angular/core';

@Component({
  selector: 'app-production-voucher-view',
  templateUrl: './production-voucher-view.component.html',
  styleUrls: ['./production-voucher-view.component.scss']
})
export class ProductionVoucherViewComponent implements OnInit {

  @Output() cancelData = new EventEmitter();
  @Input('ViewData') ViewData;

  pvData:any;

  constructor() { }

  ngOnInit() {
    this.pvData = this.ViewData;
  }

  cancelBtn() {
    this.cancelData.emit(true);
  }
  
}



