import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { ProductionVoucherComponent } from './production-voucher.component';

const routes: Routes = [
  {
      path: '', 
      component: ProductionVoucherComponent,
     
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProductionVoucherRoutingModule { }


